.loading_main {
  display: flex;
  justify-content: center;
}

.loading {
  margin-top: 76px;
  height: 50px;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.arr {
  transform: rotate(90deg);
  height: 0px;
}

.arr2 {
  transform: rotate(270deg);
  height: 0px;
}

.arr_main {
  display: flex;
}

@media (max-width: 700px) {
  .nav_link_sub_2_b {
    width: 46px;
  }
  .nav_link_sub2_2_b {
    font-size: 0px;
  }
  .arr {
    height: 22px;
  }
  
  .arr2 {
    height: 22px;
  }
}